import { Component, Element, Host, Prop, Watch, h } from '@stencil/core';
import { SharedStyles } from '../../utils/shared-styles';

enum OneSideDirection {
  Left = 'left',
  Right = 'right',
  Top = 'top',
  Bottom = 'bottom',
  None = '',
}
@Component({
  tag: 'storm-timeline',
  styleUrl: 'storm-timeline.css',
  shadow: true,
})
export class StormTimeline {
  @Element() host: HTMLElement;

  @Prop() horizontalMinHeight: string = '20rem';
  @Prop() darkMode: boolean = false;
  @Prop() oneSide: string = OneSideDirection.None;
  @Prop() horizontal: boolean = false;
  @Prop() whiteIcon: boolean = true;
  @Prop() dataSet: ITimelineItem[];
  @Prop() timelineClass: Record<string, boolean>;
  @Prop() iconContainerClass: Record<string, boolean>;
  @Prop() iconClass: Record<string, boolean>;
  @Prop() titleClass: Record<string, boolean>;
  @Prop() timeClass: Record<string, boolean>;
  @Prop() contentClass: Record<string, boolean>;

  private _dataSet: ITimelineItem[];
  private defaultTimelineClass: Record<string, boolean>;
  private defaultIconContainerClass: Record<string, boolean>;
  private defaultIconClass: Record<string, boolean>;
  private defaultTitleClass: Record<string, boolean>;
  private defaultTimeClass: Record<string, boolean>;
  private defaultContentClass: Record<string, boolean>;

  @Watch('dataSet')
  dataSetWatcher(newValue: ITimelineItem[]) {
    this._dataSet = newValue;
  }

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');

    this.dataSetWatcher(this.dataSet);
  }

  componentDidRender() {
    if (!this._dataSet) return;
  }

  iconRender(icon: string, iconColor: string) {
    if (!icon) return;
    return (
      <span
        class={{
          ...this.defaultIconContainerClass,
          ...this.iconContainerClass,
        }}
        style={{ backgroundColor: iconColor || '#E91E63' }}
      >
        <i
          class={{
            ...{ 'text-dark': !this.whiteIcon },
            ...{ 'text-white': this.whiteIcon },
            ...this.defaultIconClass,
            ...this.iconClass,
          }}
        >
          {icon}
        </i>
      </span>
    );
  }

  defaultClasses() {
    this.defaultTimelineClass = { timeline: true };
    this.defaultIconContainerClass = { 'timeline-step': true, 'p-1': true };
    this.defaultIconClass = { 'material-icons': true, 'text-sm': true, 'opacity-10': true };
    this.defaultTitleClass = { 'text-sm': true, 'font-weight-bold': true, 'mb-0': true, 'mt-0': true };
    this.defaultTimeClass = { 'text-xs': true, 'text-secondary': true, 'mt-1': true, 'mb-0': true };
    this.defaultContentClass = { 'text-sm': true, 'mt-3': true, 'mb-2': true };
  }

  timelineItemRender() {
    if (!this.dataSet) return;

    return this._dataSet?.map(e => {
      return (
        <div
          class={{
            ...{ 'timeline-block': true },
            ...{ 'mb-3': !this.horizontal },
            ...{ 'me-3': this.horizontal },
          }}
        >
          {this.iconRender(e.icon, e.iconColor)}
          <div class="timeline-content pt-1 pb-1">
            <h6
              class={{
                ...this.defaultTitleClass,
                ...{ 'text-dark': !this.darkMode },
                ...{ 'text-white': this.darkMode },
                ...{ 'text-center': this.horizontal },
                ...this.titleClass,
              }}
            >
              {e.title}
            </h6>
            <p
              class={{
                ...this.defaultTimeClass,
                ...{ 'text-center': this.horizontal },
                ...this.timeClass,
              }}
            >
              {e.time}
            </p>
            <p
              class={{
                ...this.defaultContentClass,
                ...{ 'text-dark': !this.darkMode },
                ...{ 'text-white': this.darkMode },
                ...this.contentClass,
              }}
            >
              {e.content}
            </p>
          </div>
        </div>
      );
    });
  }

  render() {
    this.defaultClasses();

    return (
      <Host>
        <div
          class={{
            ...this.defaultTimelineClass,
            ...{ 'timeline-dark': this.darkMode },
            ...{
              'timeline-one-side':
                this.oneSide == OneSideDirection.Left || this.oneSide == OneSideDirection.Right || this.oneSide == OneSideDirection.Top || this.oneSide == OneSideDirection.Bottom,
            },
            ...{ 'timeline-left-side': this.oneSide == OneSideDirection.Left && !this.horizontal },
            ...{ 'timeline-right-side': this.oneSide == OneSideDirection.Right && !this.horizontal },
            ...{ 'timeline-top-side': this.oneSide == OneSideDirection.Top && this.horizontal },
            ...{ 'timeline-bottom-side': this.oneSide == OneSideDirection.Bottom && this.horizontal },
            ...{ horizontal: this.horizontal },
            ...this.timelineClass,
          }}
          style={{ minHeight: this.horizontalMinHeight, overflow: 'auto' }}
        >
          {this.timelineItemRender()}
        </div>
      </Host>
    );
  }
}

export interface ITimelineItem {
  icon: string;
  iconColor: string;
  title: string;
  time: string;
  content: string;
}
