/* Normal Timeline Classes */
.timeline {
  position: relative;
}
.timeline:before {
  content: '';
  position: absolute;
  top: 0;
  left: 1rem;
  height: 100%;
  border-right: 2px solid #e5e5e5;
}
.timeline.timeline-dark:before {
  border-right-color: #4a4a4a;
}
.timeline-block {
  position: relative;
}
.timeline-block:after {
  content: "";
  display: table;
  clear: both;
}
.timeline-block:first-child {
  margin-top: 0;
}
.timeline-block:last-child {
  margin-bottom: 0;
}
.timeline-step {
  position: absolute;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  left: 0;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background: #fff;
  text-align: center;
  transform: translateX(-50%);
  font-size: 1rem;
  font-weight: 600;
  z-index: 1;
}
.timeline-step svg,
.timeline-step i {
  line-height: 1.4;
}
.timeline-content {
  position: relative;
  margin-left: 45px;
  margin-right: 45px;
  padding-top: 0.35rem;
  position: relative;
  top: -6px;
}
.timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.timeline:before {
  left: 50%;
  margin-left: -1px;
}
.timeline-step {
  left: 50%;
}
.timeline-content {
  width: 38%;
}
.timeline-block:nth-child(even) .timeline-content {
  float: right;
}

.timeline-one-side:before {
  left: calc(1rem + 1px);
}
.timeline-one-side .timeline-step {
  left: calc(1rem + 1px);
}
.timeline-one-side .timeline-content {
  width: auto;
}
.timeline-one-side .timeline-block:nth-child(even) .timeline-content {
  float: none;
}

/* Custom Timeline Classes WIP */
.timeline:not(.timeline-one-side) .timeline-block:nth-child(even) .timeline-content {
  float: right;
}
.timeline:not(.timeline-one-side):not(.horizontal) .timeline-block:nth-child(odd) .timeline-content {
  text-align: right;
  margin-left: 0px;
}

/* Timeline Left */
.timeline:not(.horizontal).timeline-left-side::before {
  right: calc(1rem + 1px);
}
.timeline:not(.horizontal).timeline-left-side .timeline-step {
  left: auto;
  right: calc(-1rem - 1px);
}
.timeline:not(.horizontal).timeline-left-side .timeline-content {
  display: flex;
  flex-direction: column;
  align-items: end;
  text-align: right;
}

/* Timeline Right */
.timeline:not(.horizontal).timeline-right-side::before {
  left: calc(1rem + 1px);
}
.timeline:not(.horizontal).timeline-right-side .timeline-step {
  right: auto;
  left: calc(1rem + 1px);
}
.timeline:not(.horizontal).timeline-right-side .timeline-content {
  display: flex;
  flex-direction: column;
  align-items: start;
  text-align: left;
}

/* Horizontal Timeline Classes */
.timeline.horizontal {
  display: flex;
  flex-direction: row;
  align-items: stretch;
}
.timeline.horizontal::before {
  position: absolute;
  left: 0%;
  top: 50%;
  width: 100%;
  height: 0%;
  border-right: none;
  border-top: 2px solid #e5e5e5;
}
.timeline.horizontal .timeline-block {
  box-sizing: border-box;
  display: flex;
  align-items: center;
}
.timeline.horizontal .timeline-block .timeline-step {
  left: 50%;
}
.timeline.horizontal .timeline-block .timeline-content {
  width: 100%;
  height: 38%;
  text-align: start;
  margin-left: 0%;
  margin-right: 0%;
  display: flex;
}
.timeline.horizontal:not(.timeline-one-side) .timeline-block:nth-child(odd) .timeline-content {
  flex-direction: column;
  align-self: flex-end;
}
.timeline.horizontal:not(.timeline-one-side) .timeline-block:nth-child(even) .timeline-content {
  transform: translateY(1rem);
  flex-direction: column-reverse;
  align-self: flex-start;

}

/* Timeline Top */
.timeline.horizontal.timeline-top-side::before {
  top: auto;
  bottom: calc(1rem + 1px);
}
.timeline.horizontal.timeline-top-side .timeline-step {
  bottom: 0%;
}
.timeline.horizontal.timeline-top-side .timeline-block .timeline-content {
  transform: translateY(-4rem);
  flex-direction: column-reverse;
  align-self: flex-end;
}

/* Timeline Bottom */
.timeline.horizontal.timeline-bottom-side::before {
  top: calc(1rem + 1px);
  bottom: auto;
}
.timeline.horizontal.timeline-bottom-side .timeline-step {
  top: 0%;
}
.timeline.horizontal.timeline-bottom-side .timeline-block .timeline-content {
  transform: translateY(4rem);
  flex-direction: column;
  align-self: flex-start;
}

/* Common Classes */
.material-icons {
  font-family: 'Material Icons Round';
  font-weight: normal;
  font-style: normal;
  font-size: 20px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
}

